<template>
  <div class="page page_contacts bg_black">
    <section class="">
      <div class="contact-border">
        <div class="table">
          <div class="table-cell">
            <div class="row">
              <div class="col-md-12">
                <div class="contacts-text text-center">
                  <h1 class="title-lines"><span>{{page_contact[$route.params.lang]['title']}}</span></h1>
