                  <p class="contact__text">{{page_contact[$route.params.lang]['text']}}</p>
                </div>
              </div>
              <div class="col-md-8 col-md-offset-2 col-lg-6 col-lg-offset-3">
                <form class="contact-form">
                  <div class="row m0">
                    <div class="col-xs-6">
                      <div class="form-group">
                        <input
                          type="text"
                          id="name"
                          class="form-control input"
                          :placeholder="page_contact[$route.params.lang]['form_placeholders']['name']"
                          v-model.lazy="form_data.name">
                      </div>
                    </div>
                    <div class="col-xs-6">
                      <div class="form-group">
                        <input
                          type="text"
                          id="phone"
